  search() {
    var matches = [];
    var a = [];
    a = this.optionsModel;
    var b = [];
    b = this.tablesSchemas;
    for (var i = 0; i < a.length; i++) {
      var result = b.find(function (obj) { return obj.name === a[i]; });
      matches.push({ result });
    }
    return matches;
  }

